@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/open-sans-400.woff2") format("woff2"), url("../fonts/open-sans-400.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/open-sans-700.woff2") format("woff2"), url("../fonts/open-sans-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  background-color: #2aab6d;
  text-align: center;
  padding: 0;
  border: none;
  cursor: pointer;
}

.page-body {
  font-family: "Open-Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #283645;
  background-color: #ffffff;
}

.main-nav {
  background-color: #283645;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__item a {
  display: block;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  color: #ffffff;
}

.intro {
  background-color: #1d2631;
  background-image: url("../img/back-intro-mobile.jpg");
  background-repeat: no-repeat;
}

.intro__slogan {
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
  color: #ffffff;
}

.intro__caption {
  font-size: 24px;
  line-height: 32px;
  font-weight: 300;
  color: #ffffff;
}

.download__link {
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  color: #ffffff;
}

.download__description {
  font-weight: 300;
  color: #ffffff;
}

.features__promo-text {
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
}

.features__background {
  background-image: url("../img/grey-background.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.features__item-title {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
}

.research {
  display: none;
  background-color: #ffffff;
}

.research__title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
}

.research__link {
  color: #d22856;
  text-decoration: underline;
}

.reviews__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.reviews__name {
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
}

.reviews__status {
  display: none;
  line-height: 30px;
}

.reviews__text {
  line-height: 30px;
}

.price {
  background-image: url("../img/grey-background.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.price-table__head {
  color: #ffffff;
  background-color: #283645;
}

.price-table__head-title {
  font-size: 16px;
  line-height: 30px;
  font-weight: 700;
}

.price-table__price {
  font-size: 18px;
  line-height: 30px;
  font-weight: 300;
}

.price-table__body {
  background-color: #ffffff;
}

.price-table__body td {
  line-height: 30px;
}

.contacts__description {
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
}

.contacts__info-caption {
  font-size: 18px;
  font-weight: 700;
}

.contacts__link,
.contacts__location {
  font-size: 24px;
  line-height: 32px;
  font-weight: 300;
  color: #d22856;
}

.map {
  background-color: #f2f2f2;
}

.gallery__promo-text {
  font-weight: 300;
}

.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.photo-card {
  margin: 0;
}

.photo-card__name {
  font-weight: 700;
}

.photo-card__time {
  font-size: 12px;
  font-weight: 300;
}

.photo-card__counter {
  font-size: 12px;
  color: #d22856;
}

.photo-filter {
  background-color: #f2f2f2;
}

.photo-filter__button {
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  background-color: inherit;
}

.photo-filter__button--post {
  color: #d22856;
}

.photo-filter__button--cancel {
  opacity: 0.1;
}

.feedback__promo-text {
  line-height: 30px;
}

.feedback__fieldset {
  margin: 0;
  padding: 0;
}

.feedback__fieldset-title {
  line-height: 18px;
  color: #d22856;
}

.feedback__initials-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.feedback__initials-label,
.feedback__contacts-label {
  line-height: 30px;
  color: #d22856;
  cursor: pointer;
}

.feedback__initials-input,
.feedback__contacts-input {
  font-size: 18px;
  line-height: 30px;
  color: #283645;
  padding: 0;
}
.feedback__initials-input::placeholder,
.feedback__contacts-input::placeholder {
  color: inherit;
}

.feedback__achievements,
.feedback__apps {
  margin: 0;
  padding: 0;
  list-style: none;
}

.feedback__achievements-label,
.feedback__apps-label {
  font-size: 18px;
  line-height: 30px;
  cursor: pointer;
}

.feedback__comment {
  font-size: 18px;
  line-height: 30px;
  color: #283645;
}
.feedback__comment::placeholder {
  color: inherit;
}

.feedback__footnote {
  line-height: 30px;
}
.feedback__footnote span {
  color: #d22856;
}

.copyright__caption {
  line-height: 18px;
}

.failure-popup__caption {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
}

.success-popup__caption {
  font-size: 18px;
  font-weight: 700;
  color: #000000;
}

.success-popup__background {
  background-color: #f2f2f2;
}